            line-height: 2;

            &:hover{
                background-color:$main-blue-color;
                color: white;
                cursor: pointer;
            }
        }

        
    }

}

.reply-input-box{
    display: flex;
    align-items: center;
  
    .user-reply-name{
        color: #3B5998;
        font-weight: bold;
        margin-right: 10px;
        width: 90px;
    
    }
    .reply-input{
        font-family: 'Roboto', sans-serif;
        margin: 10px 5px 0px 0px;
        padding:4px;
        width: 100%;
        min-height: 50px;
        resize: vertical;
        height: 50px;
        border: 2px solid rgb(221, 223, 226);
        box-sizing: border-box;
    }
}
