}

.comments-actions{
    grid-area: actions;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    font-size: 11px;
    color: #3B5998;

    .comment-like,
    .comment-reply,
    .comment-time{
    padding: 2px;
    padding-top: 0px;
    margin: 2px 0;
    margin-top: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    };

    .comment-like:hover,
    .comment-reply:hover{
        cursor: pointer;
    }
    .comment-time{
        grid-area: time;
        color: $color-gray;
    }
};


%hover-box{
    position: absolute;
    padding: 0px 5px;
    border: 1px solid #dddddd;
    border-radius: 2px;
    z-index: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    background-color: white;
    width: 160px;
    height: 25px;
    overflow: hidden;
    box-shadow: 3px 4px 11px 0px #888888;
}

.hide{
 display: none !important
}
